/* Reset simplu */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  color: #333;
}

body {
  background-color: #f4f7fb;
  line-height: 1.6;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

/* Container general */
.app {
  max-width: 900px;
  margin: 40px auto;
  background: white;
  box-shadow: 0 4px 20px rgb(0 0 0 / 0.1);
  border-radius: 12px;
  padding: 30px 40px;
}

/* Header */
.header {
  text-align: center;
  margin-bottom: 40px;
}

.header h1 {
  font-size: 3rem;
  color: #004aad;
  letter-spacing: 3px;
  font-weight: 900;
  margin-bottom: 6px;
}

.header p {
  font-size: 1.3rem;
  color: #555;
  font-style: italic;
}

/* Main content */
main {
  display: flex;
  flex-direction: column;
  gap: 40px;
}

/* Lista servicii */
.services-list h2,
.calculator h2,
.contact h2 {
  font-size: 1.8rem;
  color: #004aad;
  margin-bottom: 20px;
  border-bottom: 2px solid #004aad;
  padding-bottom: 6px;
  font-weight: 700;
}

/* Lista de servicii */
.services-list ul {
  list-style: none;
}

.services-list li {
  background: #e7f0ff;
  border-radius: 10px;
  padding: 20px 25px;
  margin-bottom: 15px;
  box-shadow: 0 2px 8px rgb(0 0 0 / 0.08);
  transition: background-color 0.3s ease;
  position: relative;
  cursor: default;
  border: 2px solid transparent;
}

.services-list li:hover {
  background: #d0e3ff;
  border-color: #004aad;
}

.services-list li.active {
  background-color: #004aad;
  color: #fff;
  border-color: #003c8f;
}

.services-list li label {
  cursor: pointer;
  font-weight: 700;
  font-size: 1.15rem;
  user-select: none;
  display: flex;
  align-items: center;
  gap: 10px;
}

.services-list li p {
  margin-top: 8px;
  font-size: 0.95rem;
  color: inherit;
  max-width: 700px;
}

/* Checkbox */
.services-list input[type="checkbox"] {
  width: 22px;
  height: 22px;
  cursor: pointer;
}

/* Input numar participanti */
.participants-input {
  margin-top: 15px;
  display: flex;
  align-items: center;
  font-weight: 600;
  color: inherit;
}

.participants-input label {
  font-size: 1rem;
}

.participants-input input[type="number"] {
  width: 80px;
  margin-left: 15px;
  padding: 8px 10px;
  font-size: 1rem;
  border-radius: 8px;
  border: 1.8px solid #004aad;
  outline-offset: 2px;
  transition: border-color 0.3s ease;
  background-color: #fff;
  color: #004aad;
  font-weight: 700;
  text-align: center;
  cursor: text;
}

.participants-input input[type="number"]:focus {
  border-color: #003c8f;
  box-shadow: 0 0 5px #004aadaa;
}

/* Calculator total */
.calculator .total {
  background-color: #004aad;
  color: #fff;
  padding: 25px 30px;
  border-radius: 12px;
  font-size: 1.6rem;
  font-weight: 800;
  text-align: center;
  box-shadow: 0 6px 15px rgb(0 74 173 / 0.6);
}

/* Contact */
.contact p {
  font-size: 1rem;
  margin-bottom: 10px;
  color: #444;
}

.contact a {
  color: #004aad;
  font-weight: 700;
  text-decoration: none;
  transition: color 0.3s ease;
}

.contact a:hover {
  color: #002e7a;
  text-decoration: underline;
}

/* Footer */
footer {
  text-align: center;
  margin: 35px 0 20px;
  font-size: 0.9rem;
  color: #666;
  font-style: italic;
}

/* Responsive */
@media (max-width: 600px) {
  .app {
    padding: 20px 25px;
    margin: 20px;
  }

  .header h1 {
    font-size: 2.2rem;
  }

  .header p {
    font-size: 1.1rem;
  }

  .services-list li p {
    font-size: 0.9rem;
  }

  .participants-input input[type="number"] {
    width: 60px;
  }
}
